import { Container, IResourceDictionary } from "pixi.js"
import { MapBase } from "./maps/map-base"

export default class GameBase {
  protected _stage: Container
  protected readonly _resources: IResourceDictionary

  protected _isRunning: boolean = true
  protected _map?: MapBase

  protected _elapsedSeconds = 0
  protected _startOffset?: number

  constructor(
    resources: IResourceDictionary,
    stage: Container
  ) {
    this._stage = stage
    this._resources = resources
  }

  public tick(elapsed: number, delta: number) {}
}
