import {
  Container,
  IResourceDictionary,
  ITextureDictionary,
  Sprite,
  Texture
} from "pixi.js"
import { AnimationBase } from "../objects/animation-base"
import { easing, tween } from "popmotion"
import { Config } from "../../game/config"

/**
 *
 */
export class MapBase extends AnimationBase {
  private readonly _explorationOffset: number
  protected _textures?: ITextureDictionary
  protected _backgroundContainer: Container
  protected _actorContainer: Container
  protected _foregroundContainer: Container

  public withHud: boolean = true
  public withCrosshair: boolean = true
  public withPlayer: boolean = true
  public withMouse: boolean = true
  public withDarkness: boolean = false

  constructor(
    resources: IResourceDictionary,
    mapToLoad?: string,
    onDone?: () => void
  ) {
    super(resources)

    this._onDone = onDone
    this._explorationOffset = Config.constants.width / 3

    if (mapToLoad) {
      // load the clips for this object
      this._textures = resources[mapToLoad].textures
    }

    /**
     * setup background
     * @type {Container}
     * @private
     */
    this._backgroundContainer = new Container()
    this.addChild(this._backgroundContainer)

    /**
     * setup actors
     * @type {Container}
     * @private
     */
    this._actorContainer = new Container()
    this.addChild(this._actorContainer)

    /**
     * setup foreground
     * @type {Container}
     * @private
     */
    this._foregroundContainer = new Container()
    this.addChild(this._foregroundContainer)
  }

  /**
   *
   * @returns {Container}
   */
  get actorContainer() {
    return this._actorContainer
  }

  /**
   *
   * @returns {Container}
   */
  get backgroundContainer() {
    return this._backgroundContainer
  }

  get foregroundContainer() {
    return this._foregroundContainer
  }

  /**
   *
   * @private
   */
  protected _setup() {
    // stub
  }

  /**
   *
   * @param playerX
   * @param scrollAmount
   */
  public shallScroll(
    playerX: number,
    scrollAmount: number
  ) {
    if (
      // move right
      (scrollAmount > 0 &&
        playerX + this._explorationOffset >
          Config.constants.width) ||
      // move left
      (scrollAmount < 0 &&
        playerX - this._explorationOffset <
          this._explorationOffset)
    ) {
      // move map based on positive or negative scroll amount
      this.x -= scrollAmount

      // left bound
      if (this.x > 0) {
        this.x = 0
      }

      // right bound
      if (this.x < Config.constants.width - this.width) {
        this.x = Config.constants.width - this.width
      }
    }
  }

  protected async fade() {
    const fade = new Sprite(Texture.WHITE)

    fade.alpha = 0
    fade.width = Config.constants.width
    fade.height = Config.constants.height
    fade.tint = 0x000000

    this._foregroundContainer.addChild(fade)

    return new Promise((resolve) => {
      const min = 0
      const max = 1
      tween({
        from: min,
        to: max,
        duration: 1000,
        ease: [easing.createExpoIn(2)]
      }).start({
        complete: resolve,
        update: (v: number) => (fade.alpha = v)
      })
    })
  }
}
